@import url(https://fonts.googleapis.com/css?family=Spicy+Rice&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.header {
  position: fixed;
  height: 50px;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: 'Spicy Rice', serif;
  min-height: 50px;
  font-size: 1.2em;
  border-bottom: 2px solid #379575;
  z-index: 100;
  background-color: #379575; }
  .header a {
    text-decoration: none;
    letter-spacing: 1px;
    color: white;
    border-bottom: 2px solid #379575; }
  .header a:hover, .header .selected {
    border-bottom: 2px solid white; }

.about {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: column nowrap;
  color: #379575;
  z-index: 10; }
  .about .title {
    display: flex;
    justify-content: center;
    margin: 50px 0 50px 0;
    font-size: 4em;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; }
    .about .title h1 {
      padding: 10px 0 10px 0;
      border-top: 1px solid #379575;
      border-bottom: 1px solid #379575; }
  .about .description {
    display: flex;
    justify-content: center;
    align-items: center; }
    .about .description img {
      width: 20%;
      border-radius: 50%; }
    .about .description p {
      margin: 0 8em 0 8em;
      width: 40%;
      text-align: justify; }
      .about .description p span {
        font-weight: bold; }
  .about .contact {
    width: 50%;
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    height: 150px; }
    .about .contact a {
      text-decoration: none;
      color: #379575;
      border-bottom: 1px solid white; }
    .about .contact a:hover {
      border-bottom: 1px solid #379575; }
    .about .contact .contact-card {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      flex-flow: column nowrap;
      margin: 10px; }
      .about .contact .contact-card img {
        width: 50px; }

.portfolio {
  background-color: #379575;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-flow: column nowrap;
  height: 50%;
  width: 100%;
  color: white; }
  .portfolio h1 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px 0 10px 0;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    display: flex;
    justify-content: center;
    margin: 50px 0 30px 0;
    font-size: 4em;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; }
  .portfolio .project-container {
    display: flex;
    border: 1px solid red; }
  .portfolio .projectCard {
    background: white;
    width: 300px;
    margin: 10px;
    flex-grow: 1;
    color: #379575; }
    .portfolio .projectCard img {
      width: inherit; }
    .portfolio .projectCard h3 {
      font-size: 1.5em;
      margin: 8px 0 8px 0; }
    .portfolio .projectCard p {
      text-align: left;
      text-align: initial;
      padding: 15px; }

.tech {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
  color: #379575; }
  .tech h1 {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px 0 10px 0;
    border-top: 1px solid #379575;
    border-bottom: 1px solid #379575;
    display: flex;
    justify-content: center;
    margin: 50px 0 0px 0;
    font-size: 4em;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; }
  .tech .tech-container {
    display: grid;
    grid-template-columns: 18vh 18vh 18vh 18vh 18vh;
    grid-row-gap: 4vh;
    grid-column-gap: 8vw;
    justify-content: center;
    margin: 5vh 0 5vh 0; }
  .tech .techCard {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    width: 20vh; }
    .tech .techCard p {
      padding: 5px;
      font-family: Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif; }
    .tech .techCard .techCard-image {
      height: 11vh; }

body {
  font-family: Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif; }

.App {
  display: flex;
  text-align: center;
  width: 100%;
  justify-content: center;
  align-content: center;
  flex-flow: column nowrap; }
  .App .main {
    width: 100%;
    display: flex;
    margin-top: 50px;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap; }

